@charset "UTF-8";
.CombinationsContainer {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  align-items: stretch; }
  .CombinationsContainer .sorting-option {
    position: relative;
    display: flex;
    align-items: center;
    padding: 0; }
    .CombinationsContainer .sorting-option select {
      align-self: stretch;
      flex: 1;
      padding: 8px; }
    .CombinationsContainer .sorting-option:after {
      position: absolute;
      right: 8px;
      content: '▼'; }
  .CombinationsContainer .combinations {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: -1px; }
    .CombinationsContainer .combinations .combination {
      flex: 1 0 160px;
      display: flex;
      border-top: 1px solid #BBBBBB;
      border-bottom: 1px solid #BBBBBB; }
      .CombinationsContainer .combinations .combination .number {
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #BBBBBB;
        width: 2em;
        color: white;
        font-size: .8em;
        font-weight: bold; }
      .CombinationsContainer .combinations .combination .preview {
        flex: 1;
        cursor: pointer; }
